    "designDepthes": ["", "", "+"],
    "designModes": [{
      "value": "free",
      "label": "",
      "description": ""
    },{
      "value": "invite",
      "label": "",
      "description": ""
    },{
      "value": "specify",
      "label": "",
      "description": ""
    }]
  },
  "en": {
    "": "Edit project",
    "": "Perfecting detailed project information is a necessary condition for accurately matching designers and achieving ideal results.",
