  }
}
</i18n>

<template>
  <div class="main-container">
    <div
      v-loading="loading"
      class="card">
      <div class="border-bottom center p-24">
        <h1
          v-t="''"
          class="m0 color-primary"/>
        <p
          v-t="''"
          class="m0 pt1 black-45 f-12" />
      </div>
      <el-form
        ref="form"
        :model="form"
        :rules="rules"
        :disabled="!editable"
        label-position="top"
        class="form"
        size="small">
        <el-form-item
          :label="$t('')"
          prop="title">
          <el-input
            v-model="form.title"
            :placeholder="$t('200')"
            maxlength="200"/>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="types">
          <el-checkbox
            v-for="type in $t('types')"
            v-model="form.types"
            :label="type"
            :key="type"/><el-checkbox v-model="formOthers.types.checked">
              {{ $t('') }}
              <el-input
                v-if="formOthers.types.checked"
                v-model="formOthers.types.input"
                :placeholder="$t('50')"
                maxlength="50"/>
            </el-checkbox>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="features">
          <el-checkbox
            v-for="feature in $t('features')"
            v-model="form.features"
            :label="feature"
            :key="feature"/><el-checkbox v-model="formOthers.features.checked">
              {{ $t('') }}
              <el-input
                v-if="formOthers.features.checked"
                v-model="formOthers.features.input"
                :placeholder="$t('50')"
                maxlength="50"/>
            </el-checkbox>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="keywords">
          <div
            v-for="(keyword, index) in form.keywords"
            :key="index"
            class="keyword-item">
            <el-input
              v-model="form.keywords[index]"
              :placeholder="$t('50')"
              maxlength="50" />
            <el-button @click.prevent="removeKeyword(index)">{{ $t('g.delete') }}</el-button>
          </div>
          <el-button
            v-if="form.keywords.length < 10"
            @click="addKeyword">{{ $t('') }}</el-button>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="description">
          <el-input
            v-model="form.description"
            :rows="7"
            :placeholder="$t('')"
            type="textarea"/>
          <my-upload
            ref="upload"
            :max-size="50"
            type="project_file"
            @start="onUploadStart"
            @success="onUploadSuccess"
            @error="onUploadError"
            @remove="onUploadRemove"/>
          <my-alert
            v-if="form.project_file_url"
            class="mt-12"><a
              :href="form.project_file_url"
              target="_blank">{{ $t('') }}</a>
            <a
              class="ml1"
              @click="form.project_file_url = null">{{ $t('') }}</a>
          </my-alert>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="depth">
          <el-radio-group v-model="form.depth">
            <el-radio
              v-for="depth in $t('designDepthes')"
              :label="depth"
              :key="depth"/>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="delivery_time">
          <el-radio-group v-model="form.delivery_time">
            <el-radio
              v-for="time in $t('deliveryTimes')"
              :label="time"
              :key="time"/><el-radio
                label="other">
                {{ $t('') }}
                <el-input
                  v-if="form.delivery_time === 'other'"
                  v-model="formOthers.delivery_time.input"
                  :placeholder="$t('50')"
                  maxlength="50"/>
              </el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="find_time">
          <el-radio-group v-model="form.find_time">
            <el-radio
              v-for="time in $t('findTimes')"
              :label="time"
              :key="time" /><el-radio
                label="other">
                {{ $t('') }}
                <el-input
                  v-if="form.find_time === 'other'"
                  :placeholder="$t('50')"
                  v-model="formOthers.find_time.input"
                  maxlength="50"/>
              </el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="payment">
          <el-input
            v-model="form.payment"
            maxlength="200" />
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="mode">
          <el-radio-group v-model="project.mode">
            <el-radio
              v-for="mode in $t('designModes')"
              :key="mode.value"
              :label="mode.value"
              disabled>
              <el-tooltip
                :content="mode.description"
                effect="dark"
                placement="top-start">
                <span>
                  {{ mode.label }}
                  <i class="el-icon-info" />
                </span>
              </el-tooltip>
            </el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="remark">
          <el-input
            v-model="form.remark"
            :rows="7"
            :placeholder="$t('')"
            type="textarea"/>
        </el-form-item>
        <el-form-item
          v-if="rereviewable"
          :label="$t('')">
          <el-checkbox v-model="form.re_review">{{ $t('') }}</el-checkbox>
        </el-form-item>
      </el-form>
      <p
        v-t="''"
        class="m0 pb-24 center f-14 black-45" />
    </div>
    <el-button
      :disabled="loading || !editable"
      :loading="submitting"
      class="block w-100 shadow mt-24"
      type="primary"
      @click="onSubmit">{{ $t('g.submitBtn') }}</el-button>
      <!-- <preview-dialog
      ref="previewDialog"
      action-type="edit" /> -->
  </div>
</template>

<script>
/* eslint eqeqeq: "off" */
import PreviewDialog from '../publish/components/PreviewDialog'
import { getProjectById, updateProjectById } from '@/api/project'
import { Project } from '@/services/constants'
export default {
  components: { PreviewDialog },
  data () {
    const getCheckBoxValidator = (field) => {
      return (rule, value, callback) => {
        let formOther = this.formOthers[field]
        if (!value.length && !formOther.checked) {
          callback(new Error(this.$t('')))
        } else if (formOther.checked && !formOther.input) {
          callback(new Error(this.$t('')))
        } else {
          callback()
        }
      }
    }
    const getRadioValidator = (field) => {
      return (rule, value, callback) => {
        let formOther = this.formOthers[field]
        if (!value) {
          callback(new Error(this.$t('')))
        } else if (value === 'other' && !formOther.input) {
          callback(new Error(this.$t('')))
        } else {
          callback()
        }
      }
    }
    return {
      // 
      formOthers: {
        types: {
          checked: false,
          input: ''
        },
        features: {
          checked: false,
          input: ''
        },
        delivery_time: {
          input: ''
        },
        find_time: {
          input: ''
        }
      },
      form: {
        title: '',
        types: [],
        features: [],
        keywords: [],
        depth: '',
        description: '',
        project_file_url: null,
        delivery_time: '',
        payment: '',
        find_time: '',
        remark: '',
        re_review: false // 
      },
      rules: {
        title: { required: true, message: this.$t('') },
        types: { required: true, validator: getCheckBoxValidator('types') },
        features: { required: true, validator: getCheckBoxValidator('features') },
        keywords: [{
          type: 'array',
          max: 10,
          message: this.$t('10'),
          defaultField: { required: true, type: 'string', max: 50, message: this.$t('') }
        }, {
          validator: (rule, value, callback) => {
            const s = new Set(value)
            if (s.size !== value.length) {
              callback(new Error(this.$t('')))
            } else {
              callback()
            }
          }
        }],
        depth: { required: true, message: this.$t('') },
        description: { required: true, message: this.$t(''), whitespace: true },
        delivery_time: { required: true, validator: getRadioValidator('delivery_time') },
        payment: { required: true, message: this.$t('') },
        find_time: { required: true, validator: getRadioValidator('find_time') }
      },
      // 
      project: {
        mode: '',
        status: null
        // ... form
      },
      uploading: false, // 
      loading: true, // 
      submitting: false
    }
  },
  computed: {
    id () {
      return this.$route.params.id
    },
    // 
    editable () {
      const { project } = this
      return project.user_id == this.$uid() && // 
        (project.status == Project.STATUS_REVIEW_FAILED || // 
         project.status == Project.STATUS_REVIEWING) // 
    },
    // 
    rereviewable () {
      return this.project.status === Project.STATUS_REVIEW_FAILED
    },
    language () {
      return this.$store.getters.language
    }
  },
  watch: {
    // 
    language () {
      this.form.types = []
      this.form.features = []
    },
    'form.keywords' () {
      this.$refs.form.validateField('keywords')
    }
  },
  created () {
    getProjectById(this.id).then(({ data }) => {
      this.loading = false
      this.project = data
      this.setFormData() // Form

      if (data.user.id != this.$uid()) { // eslint-disable-line eqeqeq
        this.$router.replace('/403')
      }
      if (!this.editable) {
        this.$message.error(this.$t(''))
        
      }
    }).catch(({ statusCode }) => {
      this.loading = false
    })
  },
  methods: {
    onSubmit () {
      if (this.uploading) {
        return this.$message.warning(this.$t(''))
      }

      this.$refs.form.validate(valid => {
        if (!valid) {
          this.$notify.error({
            title: this.$t(''),
            message: this.$t('')
          })
        } else {
          // this.$refs.previewDialog.show(this.getFormData())
          this.submitting = true
          updateProjectById(this.id, this.getFormData()).then(() => {
            this.$message.success(this.$t(''))
            this.$router.push(`/project/${this.id}`)
          }).catch(() => {
            this.submitting = false
          })
        }
      })
    },
    getFormData () {
      let form = this.$_.cloneDeep(this.form)

      // 
      const formOthers = this.formOthers
      formOthers.types.checked && form.types.push(formOthers.types.input)
      formOthers.features.checked && form.features.push(formOthers.features.input)
      form.delivery_time === 'other' && (form.delivery_time = formOthers.delivery_time.input)
      form.find_time === 'other' && (form.find_time = formOthers.find_time.input)

      return form
    },
    /**
     * form
     * Bug
     */
    setFormData () {
      const { title, depth, description, project_file_url, payment, remark } = this.project

      // 
      this.form = {
        ...this.form,
        title,
        depth,
        description,
        project_file_url,
        payment,
        remark
      }

      // $t()012keyindexOf()
      const objToArray = (obj) => {
        return Object.keys(obj).map(v => {
          return obj[v]
        })
      }
      const setCheckboxValues = (field) => {
        const values = this.project[field]
        values.forEach(v => {
          if (objToArray(this.$t(field)).indexOf(v) === -1) {
            this.formOthers[field] = {
              checked: true,
              input: v
            }
          } else {
            this.form[field].push(v)
          }
        })
      }
      const setRadioValue = (field) => {
        const value = this.project[field]
        if (objToArray(this.$t(field)).indexOf(value) === -1) {
          this.form[field] = 'other'
          this.formOthers[field] = {
            input: value
          }
        } else {
          this.form[field] = value
        }
      }

      ['types', 'features', 'keywords'].forEach(v => {
        setCheckboxValues(v)
      })

      setRadioValue('delivery_time')
      setRadioValue('find_time')
    },
    addKeyword () {
      this.form.keywords.push('')
    },
    removeKeyword (index) {
      this.form.keywords.splice(index, 1)
    },
    onUploadStart () {
      this.uploading = true
    },
    onUploadSuccess (data) {
      this.uploading = false
      this.form.project_file_url = data.path
    },
    onUploadError () {
      this.uploading = false
    },
    onUploadRemove () {
      this.form.project_file_url = null
    }
  }
}
</script>

<style lang="scss" scoped>
.form {
  width: 800px;
  position: relative;
  left: 50%;
  margin-left: -400px;
  padding-top: 32px;
  & .upload {
    margin-top: 12px;
    width: 500px;
  }
  &-item--short {
    width: 360px;
  }
  /deep/ .el-input__inner,
  /deep/ .el-textarea__inner {
    border-color: rgb(220, 223, 230);
    &:focus {
      border-color: #0077b5;
    }
  }
  .el-checkbox {
    margin: 0;
    width: 33%;
    font-weight: 400;
  }
  .el-radio-group {
    display: block;
    .el-radio {
      width: 50%;
      margin: 0;
      line-height: 32px;
      font-weight: 400;
      .el-input {
        width: 300px;
      }
    }
  }
  .keyword-item {
    margin-bottom: 8px;
    .el-input {
      width: 300px;
      margin-right: 8px;
    }
  }
  .alert > a {
    line-height: 20px;
  }
}
</style>



