<i18n>
{
  "zh": {
    "tips": ""
  },
  "en": {
    "": "Edit Personal Information",
    "": "Avatar",
    "": "Name",
    "/": "Title/Company",
    "": "Introduction",
    "": "Professional Fields",
    "": "Phone",
    "": "Email",
    "": "Bank Card Number",
    "": "Bank Name",
    "": "Account Name",
    "": "Please fill in the relevant information of the bank card accurately to ensure the smooth reception of design fees.",
    "": "Have not bound email yet",
    "": "Update",
    "": "Successful Operation",
    "2~50": "2 to 50 characters",
    "50": "50 characters at most",
    "200": "200 characters at most",
    "": "Please enter your real name (between 2 to 50 characters)",
    "2MB": "File max size is 2 MB",
    "": "File uploading, please wait",
    "tips": "If you represent a company, you can enter your company name in Name or Title field, and enter your company introduction in Introduction field.",
    "": "Architectural Design",
    "": "Interior Design",
    "": "Landscape Design",
    "": "Urban Design",
    "": "Urban Planning",
    "": "Conceptual Planning"
  }
}
</i18n>

<template>
  <div class="container">
    <h2
      v-t="''"
      class="mt0" />
    <el-form
      ref="form"
      :model="form"
      :rules="rules"
      label-position="top">
      <el-form-item :label="$t('')">
        <el-upload
          v-loading="avatarUploading"
          :show-file-list="false"
          :http-request="onUpload"
          :before-upload="beforeFileUpload"
          class="avatar-uploader"
          accept="image/jpeg, image/jpg, image/png, image/bmp"
          action="">
          <img
            v-if="avatarUrl"
            :src="avatarUrl"
            class="avatar">
          <i
            v-else
            class="el-icon-plus avatar-uploader-icon"/>
        </el-upload>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        prop="name">
        <el-input
          v-model="form.name"
          :placeholder="$t('2~50')"
          maxlength="50"/>
        <el-card class="tips-card">{{ $t('tips') }}</el-card>
      </el-form-item>
      <el-form-item :label="$t('/')" >
        <el-input
          v-model="form.title"
          :placeholder="$t('50')"
          maxlength="50"/>
      </el-form-item>
      <el-form-item :label="$t('')" >
        <el-input
          v-model="form.introduction"
          :placeholder="$t('200')"
          autosize
          type="textarea"
          rows="1"
          resize="none"
          maxlength="200"/>
      </el-form-item>
      <el-form-item
        v-if="$isDesigner()"
        :label="$t('')">
        <el-checkbox-group v-model="form.professional_fields">
          <el-checkbox
            v-for="field in fields"
            :key="field"
            :label="$t(`${field}`)"/>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item :label="$t('')" >
        <el-input
          :value="$store.getters.userInfo.email || $t('')"
          disabled/>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        prop="phone">
        <el-input
          :value="$store.getters.userInfo.phone"
          disabled/>
      </el-form-item>
      <template v-if="$isDesigner()">
        <el-form-item :label="$t('')" >
          <el-input
            v-model="form.bank_card_number"
            maxlength="100"/>
          <el-card class="tips-card">
            <span v-t="''" />
          </el-card>
        </el-form-item>
        <el-form-item :label="$t('')" >
          <el-input
            v-model="form.bank_name"
            maxlength="100"/>
        </el-form-item>
        <el-form-item :label="$t('')" >
          <el-input
            v-model="form.account_name"
            maxlength="100"/>
        </el-form-item>
      </template>
      <el-form-item>
        <el-button
          :loading="loading"
          @click="onSubmit">{{ $t('') }}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { namePattern } from '@/utils/validate'
import { updateCurrentUserInfo } from '@/api/user'
import { upload } from '@/api/upload'
export default {
  data () {
    return {
      fields: ['', '', '', '', '', ''],
      form: {
        name: '',
        title: '',
        introduction: '',
        // avatar_id: null, 

        // 
        professional_fields: [],
        account_name: '',
        bank_name: '',
        bank_card_number: ''
      },
      rules: {
        name: [
          { required: true, message: this.$t('') },
          { pattern: namePattern, min: 1, max: 50, message: this.$t('2~50') }
        ]
      },
      loading: false, // 
      avatarUploading: false,
      avatarUrl: ''
    }
  },
  computed: {
    language () {
      return this.$store.getters.language
    }
  },
  watch: {
    language () {
      this.form.professional_fields = []
    }
  },
  created () {
    const user = this.$store.getters.userInfo
    Object.keys(this.form).forEach(key => {
      this.form[key] = user[key]
    })
    this.avatarUrl = user.avatar_url
  },
  methods: {
    onSubmit () {
      if (this.avatarUploading) {
        return this.$message.warning(this.$t(''))
      }
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.loading = true
          updateCurrentUserInfo(this.form).then(({ data }) => {
            this.loading = false
            this.$store.commit('SET_USERINFO', data)
            this.$message.success(this.$t(''))
          }).catch(() => {
            this.loading = false
          })
        }
      })
    },
    beforeFileUpload (file) {
      const isLessThan = file.size / 1024 / 1024 < 2
      if (!isLessThan) {
        this.$message.error(this.$t('2MB'))
      }
      return isLessThan
    },
    onUpload (file) {
      this.avatarUploading = true
      upload('avatar', file.file)
        .then(({ data }) => {
          this.avatarUploading = false
          this.form.avatar_id = data.id
          this.avatarUrl = URL.createObjectURL(file.file)
          file.onSuccess()
        }).catch(error => {
          console.error(error)
          this.avatarUploading = false
          file.onError()
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  position: relative;
  padding: 24px 32px;
  .el-form {
    width: 600px;
  }
}
.tips-card {
  position: absolute;
  top: 0px;
  left: 660px;
  width: 250px;
  font-size: 14px;
  line-height: 1.8em;
  color: rgba($color: #000000, $alpha: 0.65);
}
.avatar-uploader {
  width: 102px;
  /deep/ .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    &:hover {
      border-color: #0077b5;
    }
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
  }
  .avatar {
    width: 100px;
    height: 100px;
    display: block;
  }
}
</style>



