<i18n>
{
  "zh": {
    "modes": {
      "free": "",
      "invite": "",
      "specify": ""
    }
  },
  "en": {
    "": "Preview and submit",
    "": "Confirm project information",
    "": "Agree platform agreement",
    "": "Successfully publish project",
    "": "Project title",
    "": "Project types",
    "": "Project features",
    "": "Project description",
    "": "Project keywords",
    "": "Design depth",
    "": "Delivery time",
    "": "How long do you want to find a designer?",
    "": "How much would you want to pay for the designer?",
    "": "Download file",
    "": "Bidding mode",
    "": "Application remark",
    "": "Invited designers",
    "": " ",
    "": "I agree the above agreement",
    "": "Next",
    " ": "Submit",
    "": "Back",
    "modes": {
      "free": "Free",
      "invite": "Invite designers",
      "specify": "Specify designers"
    }
  }
}
</i18n>

<template>
  <el-dialog
    :title="$t('')"
    :close-on-click-modal="false"
    :visible.sync="visible"
    class="project-preview-dialog">
    <el-steps
      :active="step"
      simple
      finish-status="success">
      <el-step :title="$t('')" />
      <el-step :title="$t('')" />
      <el-step :title="$t('')" />
    </el-steps>
    <template v-if="step === 0">
      <div class="project-info">
        <h3 v-t="''" />
        <p>{{ form.title }}</p>
        <h3 v-t="''" />
        <p>{{ form.types.join('/') }}</p>
        <h3 v-t="''" />
        <p>{{ form.features.join('/') }}</p>
        <h3 v-t="''" />
        <template v-if="form.keywords.length">
          <el-tag
            v-for="keyword in form.keywords"
            :key="keyword"
            type="info"
            class="mr1">{{ keyword }}</el-tag>
        </template>
        <p
          v-t="''"
          v-else />
        <h3 v-t="''" />
        <p
          class="pre-wrap"
          v-text="form.description" />
        <my-alert
          v-if="form.project_file_url"
          class="mt-12">
          <a
            :href="form.project_file_url"
            target="_blank">{{ $t('') }}</a>
        </my-alert>
        <h3 v-t="''" />
        <p v-text="form.depth" />
        <h3 v-t="''" />
        <p v-text="form.delivery_time" />
        <h3 v-t="''" />
        <p v-text="form.find_time" />
        <h3 v-t="''" />
        <p v-text="form.payment" />
        <h3 v-t="''" />
        <p v-text="$t(`modes.${form.mode}`)" />
        <h3 v-t="''" />
        <p v-text="form.remark || $t('')" />
        <template v-if="invitedDesigners.length">
          <h3 v-t="''" />
          <router-link
            v-for="designer in invitedDesigners"
            :key="designer.id"
            :to="'/profile?uid=' + designer.id"
            target="_blank">
            <my-avatar
              :avatar-url="designer.avatar_url"
              class="avatar" />
          </router-link>
        </template>
      </div>
    </template>
    <template v-else>
      <div class="center">
        <p class="pre-wrap m0"> {{ agreement }}</p>
        <el-checkbox
          v-model="agree"
          class="m1">{{ $t('') }}</el-checkbox>
      </div>
    </template>
    <div class="center pt1">
      <el-button
        v-if="step === 0"
        type="primary"
        @click="step = 1">{{ $t('') }}</el-button>
      <el-button
        v-if="step === 1"
        :loading="publishing"
        type="primary"
        @click="onSubmit">{{ $t(' ') }}</el-button>
      <el-button
        v-if="step === 1"
        @click="step = 0">{{ $t('') }}</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { publishProject } from '@/api/project'
import agreement from '@/services/agreements'
export default {
  data () {
    return {
      step: 0, // 0. 1. 
      form: {
        types: [],
        features: [],
        keywords: []
        // ...
      },
      invitedDesigners: [],
      agree: false, // 
      publishing: false,
      visible: false
    }
  },
  computed: {
    agreement () {
      return agreement
    }
  },
  methods: {
    show (form, invitedDesigners = []) {
      Object.assign(this.$data, this.$options.data(), {
        form, invitedDesigners, visible: true
      })
    },
    onSubmit () {
      if (!this.agree) {
        return this.$message.warning(this.$t(''))
      }

      let { form } = this
      if (this.form.mode !== 'free') {
        form = { ...form, invited_designer_ids: this.invitedDesigners.map(v => v.id) }
      }

      this.publishing = true
      publishProject(form).then(({ data: { id } }) => {
        this.publishing = false
        this.$router.push(`/publish/result?id=${id}`)
      }).catch(() => {
        this.publishing = false
      })
    }
  }
}
</script>

<style lang="scss">
.project-preview-dialog {
  .el-dialog__header {
    border: 1px solid rgba(0, 0, 0, 0.15);
  }
  .el-dialog__body {
    color: rgba(0, 0, 0, 0.85);
  }
}
</style>

<style lang="scss" scoped>
.el-steps {
  padding: 12px 16px;
  /deep/ .el-step__title {
    font-size: 14px;
  }
}
.avatar {
  width: 32px;
  margin: 0 8px 8px 0;
}
</style>



