<i18n>
{
  "zh": {
    "countdown": "({count})"
  },
  "en": {
    "": "Reset Password",
    " / ": "Phone Number / Email",
    "": "User type",
    "": "Designer",
    "": "Party",
    "": "Send verification code",
    "": "Verification code",
    "": "New password",
    " ": "Submit",
    "": "Please enter a valid phone number or email",
    "6": "Please enter 6 characters verification code",
    "": "Please enter the original password",
    " 6  25 ": "Password length is 6 to 25 characters",
    "": "Successfully reset password",
    "": "Please select user type",
    "countdown": "Resend verification code ({count})"
  }
}
</i18n>

<template>
  <section class="container both-center">
    <h1 v-t="''" />
    <el-form
      ref="form"
      :rules="rules"
      :model="form"
      size="small">
      <el-form-item
        :label="$t('')"
        prop="type">
        <el-radio-group v-model="form.type">
          <el-radio label="party">{{ $t('') }}</el-radio>
          <el-radio label="designer">{{ $t('') }}</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item
        :label="$t(' / ')"
        prop="identifier">
        <el-input v-model="form.identifier">
          <el-button
            slot="append"
            :loading="sending"
            :disabled="!!count"
            @click="onSend">{{ sendButtonText }} </el-button>
        </el-input>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        prop="code">
        <el-input v-model="form.code"/>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        prop="password">
        <el-input
          v-model="form.password"
          type="text"/>
      </el-form-item>
      <el-form-item>
        <el-button
          :loading="loading"
          type="primary"
          @click="onSubmit">{{ $t(' ') }}</el-button>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
import { validateEmail, validatePhone } from '@/utils/validate'
import { sendCode, sendEmailCode, resetPassword } from '@/api/user'
export default {
  data () {
    return {
      form: {
        code: '',
        identifier: '',
        password: '',
        type: null
      },
      rules: {
        identifier: {
          validator: (rule, value, callback) => {
            if (!validatePhone(value) && !validateEmail(value)) {
              callback(this.$t(''))
            } else {
              callback()
            }
          },
          trigger: 'blur'
        },
        code: { required: true, len: 6, message: this.$t('6'), trigger: 'blur' },
        password: [
          { required: true, trigger: 'blur', message: this.$t('') },
          { min: 6, max: 25, trigger: 'blur', message: ' 6  25 ' }
        ],
        type: { required: true, message: this.$t('') }
      },
      loading: false,
      sending: false, // 
      count: 0, // 
      timer: null // 
    }
  },
  computed: {
    sendButtonText () {
      const { count } = this
      if (count) {
        return this.$t('countdown', { count })
      } else {
        return this.$t('')
      }
    }
  },
  methods: {
    onSend () {
      this.$refs.form.validateField('identifier', (message) => {
        if (message) return
        if (!this.form.type) return this.$message.warning(this.$t(''))

        this.sending = true
        const { identifier, type } = this.form
        const sendFn = validatePhone(identifier)
          ? () => sendCode(identifier, type, 'resetPassword')
          : () => sendEmailCode(identifier, 'resetPassword')

        sendFn().then(() => {
          this.sending = false
          this.count = 60
          this.countDown()
        }).catch(() => {
          this.sending = false
        })
      })
    },
    onSubmit () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.loading = true

          const { code, identifier, password, type } = this.form
          let form = { code, password, type }
          if (validatePhone(identifier)) {
            form.phone = identifier
          } else {
            form.email = identifier
          }

          resetPassword(form).then(() => {
            this.loading = false
            this.$message.success(this.$t(''))
            this.$router.push({
              path: '/signin',
              replace: true
            })
          }).catch(() => {
            this.loading = false
          })
        }
      })
    },
    countDown () {
      if (this.count > 0) {
        this.count--
        this.timer = setTimeout(() => {
          this.countDown()
        }, 1000)
      } else {
        clearTimeout(this.timer)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  width: 400px;
  padding: 30px;
  background: #ffffff;
  border: 1px solid #d0d3d6;
  border-radius: 4px;
}
h1 {
  margin: 0 0 24px 0;
  color: #000;
  text-align: center;
  font-size: 24px;
  font-weight: 400;
}
.el-button {
  width: 100%;
}
</style>



