    }]
  },
  "en": {
    "": "I am party",
    "": "I'm looking for designer or company to do projects",
    "": "Meet the most professional designers in the most professional and international architecture, planning, landscape, interior design platform",
    "": "Perfecting detailed project information is a necessary condition for accurately matching designers and achieving ideal results.",
    "": "Project title",
    "50": "50 characters at most",
    "200": "200 characters at most",
    "": "Project types",
    "": "Project features",
    "": "Project keywords",
    "": "Design depth",
    "": "Project description",
    "": "Such as the project area, construction budget, start time, your preferred style and color, etc.",
    "": "Delivery time",
    "": "How long do you want to find a designer?",
    "": "How much would you want to pay for the designer?",
    "": "Upload file",
    "": "Bidding mode",
    "": "Application remark",
    "": "Leave a message to the administrator explaining some important information",
    "": "After the project is approved, the designer can view and register this project.",
    "": "Other",
    "": "Publish the project",
    "50MB": "Allow upload only one file of which size is less than 50MB",
    "": "Allow upload only one file",
    "50MB": "File max size is 50MB",
    "": "File uploading, please wait",
    "": "Please enter this field",
    "": "Please select this field",
    "": "Please enter the input field",
    "10": "Can only add 10 keywords",
    "": "Each keyword must be distinct",
    "": "Your bidding mode is \"Invite Designers\". You have to invite at least one designer to participate the project.",
    "": "Your bidding mode is \"Specify Designers\". You have to invite at least one designer to participate the project.",
    "": "Error",
    "": "Please fill in all required form items in the prescribed format",
    "types":  {
      "conceptualPlanning": "Conceptual Planning",
      "urbanDesign": "Urban Design",
      "architecturalDesign": "Architectural Design",
      "landscapeArchitecture": "Landscape Architecture",
      "interiorDesign": "Interior Design"
    },
    "features": {
      "residence": "Residence",
      "commercial": "Commercial",
      "office": "Office",
      "publicSpace": "Public Space",
      "institution": "Institution",
      "retail": "Retail",
      "restaurant": "Restaurant",
      "hotel": "Hotel",
      "entertainment": "Entertainment",
      "gardenSquare": "Garden Square"
    },
    "deliveryTimes": {
      "afterOneMonth": "After one month",
      "afterThreeMonth": "After three month",
      "afterSixMonth": "After six month"
    },
    "findTimes": {
      "9to12": "9~12 days",
      "12to20": "12~20 days",
      "oneMonth": "In one month"
    },
    "designDepthes": [
      "Conceptual plan", "Plan & design", "Scheme + Preliminary design"
    ],
    "designModes": [{
      "value": "free",
      "label": "Free",
      "description": "All designers can apply for the project"
    },{
      "value": "invite",
      "label": "Invite designers",
      "description": "Only the designer who receives the invitation can participate in the project; the designer can accept or reject the invitation; the payment can be distributed to multiple designers in different shares."
    },{
      "value": "specify",
      "label": "Specify designers",
      "description": "Only designated designers can participate in the project; designers can accept or reject; each designer receives payment"
    }]
  }
}
</i18n>

<template>
  <div class="main-container">
    <div class="card">
      <div class="border-bottom center p-24">
        <h1
          v-t="''"
          class="m0 color-primary"/>
        <p
          v-t="''"
          class="m0 p1 black-65 f-13"/>
        <p
          v-t="''"
          class="m0 p0 black-45 f-12" />
      </div>
      <el-form
        ref="form"
        :model="form"
        :rules="rules"
        label-position="top"
        class="form"
        size="small">
        <el-form-item
          :label="$t('')"
          prop="title">
          <el-input
            v-model="form.title"
            :placeholder="$t('200')"
            maxlength="200"/>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="types">
          <el-checkbox
            v-for="type in types"
            v-model="form.types"
            :label="$t(`types.${type}`)"
            :key="type"/><el-checkbox v-model="formOthers.types.checked">
              {{ $t('') }}
              <el-input
                v-if="formOthers.types.checked"
                v-model="formOthers.types.input"
                :placeholder="$t('50')"
                maxlength="50"/>
            </el-checkbox>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="features">
          <el-checkbox
            v-for="feature in features"
            v-model="form.features"
            :label="$t(`features.${feature}`)"
            :key="feature"/><el-checkbox v-model="formOthers.features.checked">
              {{ $t('') }}
              <el-input
                v-if="formOthers.features.checked"
                v-model="formOthers.features.input"
                :placeholder="$t('50')"
                maxlength="50"/>
            </el-checkbox>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="keywords">
          <div
            v-for="(keyword, index) in form.keywords"
            :key="index"
            class="keyword-item">
            <el-input
              v-model="form.keywords[index]"
              :placeholder="$t('50')"
              maxlength="50" />
            <el-button @click.prevent="removeKeyword(index)">{{ $t('g.delete') }}</el-button>
          </div>
          <el-button
            v-if="form.keywords.length < 10"
            @click="addKeyword">{{ $t('') }}</el-button>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="description">
          <el-input
            v-model="form.description"
            :rows="7"
            :placeholder="$t('')"
            type="textarea"/>
          <my-upload
            ref="upload"
            :max-size="50"
            type="project_file"
            @start="onUploadStart"
            @success="onUploadSuccess"
            @error="onUploadError"
            @remove="onUploadRemove"/>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="depth">
          <el-radio-group v-model="form.depth">
            <el-radio
              v-for="depth in $t('designDepthes')"
              :label="depth"
              :key="depth"/>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="delivery_time">
          <el-radio-group v-model="form.delivery_time">
            <el-radio
              v-for="time in deliveryTimes"
              :label="$t(`deliveryTimes.${time}`)"
              :key="time"/><el-radio
                label="other">
                {{ $t('') }}
                <el-input
                  v-if="form.delivery_time === 'other'"
                  v-model="formOthers.delivery_time.input"
                  :placeholder="$t('50')"
                  maxlength="50"/>
              </el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="find_time">
          <el-radio-group v-model="form.find_time">
            <el-radio
              v-for="time in findTimes"
              :label="$t(`findTimes.${time}`)"
              :key="time" /><el-radio
                label="other">
                {{ $t('') }}
                <el-input
                  v-if="form.find_time === 'other'"
                  :placeholder="$t('50')"
                  v-model="formOthers.find_time.input"
                  maxlength="50"/>
              </el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="payment">
          <el-input
            v-model="form.payment"
            maxlength="200" />
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="mode">
          <el-radio-group v-model="form.mode">
            <el-radio
              v-for="mode in $t('designModes')"
              :key="mode.value"
              :label="mode.value">
              <el-tooltip
                :content="mode.description"
                effect="dark"
                placement="top-start">
                <span>
                  {{ mode.label }}
                  <i class="el-icon-info" />
                </span>
              </el-tooltip>
            </el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          :label="$t('')"
          prop="remark">
          <el-input
            v-model="form.remark"
            :rows="7"
            :placeholder="$t('')"
            type="textarea"/>
        </el-form-item>
      </el-form>
      <p
        v-t="''"
        class="m0 pb-24 center f-14 black-45" />
    </div>
    <invite-card
      v-if="needInvite"
      ref="inviteCard"
      class="mt-24"/>
    <el-button
      class="block w-100 shadow mt-24"
      type="primary"
      @click="onSubmit">{{ $t('') }}</el-button>
    <preview-dialog ref="previewDialog" />
  </div>
</template>

<script>
import InviteCard from '@/views/components/InviteCard'
import PreviewDialog from './components/PreviewDialog'
export default {
  components: { InviteCard, PreviewDialog },
  data () {
    const getCheckBoxValidator = (field) => {
      return (rule, value, callback) => {
        let formOther = this.formOthers[field]
        if (!value.length && !formOther.checked) {
          callback(new Error(this.$t('')))
        } else if (formOther.checked && !formOther.input) {
          callback(new Error(this.$t('')))
        } else {
          callback()
        }
      }
    }
    const getRadioValidator = (field) => {
      return (rule, value, callback) => {
        let formOther = this.formOthers[field]
        if (!value) {
          callback(new Error(this.$t('')))
        } else if (value === 'other' && !formOther.input) {
          callback(new Error(this.$t('')))
        } else {
          callback()
        }
      }
    }
    return {
      types: [
        'conceptualPlanning',
        'urbanDesign',
        'architecturalDesign',
        'landscapeArchitecture',
        'interiorDesign'
      ],
      features: [
        'residence',
        'commercial',
        'office',
        'publicSpace',
        'institution',
        'retail',
        'restaurant',
        'hotel',
        'entertainment',
        'gardenSquare'
      ],
      deliveryTimes: [
        'afterOneMonth',
        'afterThreeMonth',
        'afterSixMonth'
      ],
      findTimes: [
        '9to12',
        '12to20',
        'oneMonth'
      ],
      // 
      formOthers: {
        types: {
          checked: false,
          input: ''
        },
        features: {
          checked: false,
          input: ''
        },
        delivery_time: {
          input: ''
        },
        find_time: {
          input: ''
        }
      },
      form: {
        title: '',
        types: [],
        features: [],
        keywords: [],
        depth: '',
        description: '',
        project_file_url: null,
        delivery_time: '',
        payment: '',
        find_time: '',
        mode: '',
        remark: ''
      },
      rules: {
        title: { required: true, message: this.$t('') },
        types: { required: true, validator: getCheckBoxValidator('types') },
        features: { required: true, validator: getCheckBoxValidator('features') },
        keywords: [{
          type: 'array',
          max: 10,
          message: this.$t('10'),
          defaultField: { required: true, type: 'string', max: 50, message: this.$t('') }
        }, {
          validator: (rule, value, callback) => {
            const s = new Set(value)
            if (s.size !== value.length) {
              callback(new Error(this.$t('')))
            } else {
              callback()
            }
          }
        }],
        depth: { required: true, message: this.$t('') },
        description: { required: true, message: this.$t(''), whitespace: true },
        delivery_time: { required: true, validator: getRadioValidator('delivery_time') },
        payment: { required: true, message: this.$t('') },
        find_time: { required: true, validator: getRadioValidator('find_time') },
        mode: { required: true, message: this.$t('') }

      },
      uploading: false
    }
  },
  computed: {
    language () {
      return this.$store.getters.language
    },
    needInvite () {
      return this.form.mode === 'invite' || this.form.mode === 'specify'
    }
  },
  watch: {
    // 
    language () {
      this.form.types = []
      this.form.features = []
    },
    'form.keywords' () {
      this.$refs.form.validateField('keywords')
    }
  },
  methods: {
    onSubmit () {
      if (this.uploading) {
        return this.$message.warning(this.$t(''))
      }

      this.$refs.form.validate(valid => {
        if (this.needInvite && !this.$refs.inviteCard.invitedDesigners.length) {
          if (this.form.mode === 'invite') {
            this.$message.warning(this.$t(''))
          }
          if (this.form.mode === 'specify') {
            this.$message.warning(this.$t(''))
          }
          return
        }

        if (!valid) {
          this.$notify.error({
            title: this.$t(''),
            message: this.$t('')
          })
        } else {
          if (this.needInvite) {
            this.$refs.previewDialog.show(this.getFormData(), this.$refs.inviteCard.invitedDesigners)
          } else {
            this.$refs.previewDialog.show(this.getFormData())
          }
        }
      })
    },
    getFormData () {
      let form = this.$_.cloneDeep(this.form)

      // 
      const formOthers = this.formOthers
      formOthers.types.checked && form.types.push(formOthers.types.input)
      formOthers.features.checked && form.features.push(formOthers.features.input)
      form.delivery_time === 'other' && (form.delivery_time = formOthers.delivery_time.input)
      form.find_time === 'other' && (form.find_time = formOthers.find_time.input)

      // id
      if (this.needInvite) form.invited_user_ids = this.$refs.inviteCard.invitedDesigners.map(v => v.id)

      return form
    },
    addKeyword () {
      this.form.keywords.push('')
    },
    removeKeyword (index) {
      this.form.keywords.splice(index, 1)
    },
    onUploadStart () {
      this.uploading = true
    },
    onUploadSuccess (data) {
      this.uploading = false
      this.form.project_file_url = data.path
    },
    onUploadError () {
      this.uploading = false
    },
    onUploadRemove () {
      this.form.project_file_url = null
    }
  }
}
</script>

<style lang="scss" scoped>
.form {
  width: 800px;
  position: relative;
  left: 50%;
  margin-left: -400px;
  padding-top: 32px;
  & .upload {
    margin-top: 12px;
    width: 500px;
  }
  &-item--short {
    width: 360px;
  }
  /deep/ .el-input__inner,
  /deep/ .el-textarea__inner {
    border-color: rgb(220, 223, 230);
    &:focus {
      border-color: #0077b5;
    }
  }
  .el-checkbox {
    margin: 0;
    width: 33%;
    font-weight: 400;
  }
  .el-radio-group {
    display: block;
    .el-radio {
      width: 50%;
      margin: 0;
      line-height: 32px;
      font-weight: 400;
      .el-input {
        width: 300px;
      }
    }
  }
  .keyword-item {
    margin-bottom: 8px;
    .el-input {
      width: 300px;
      margin-right: 8px;
    }
  }
}
</style>



