<i18n>
{
  "en": {
    "": "Design fee remittance detail",
    "": "View project detail",
    "": "Remittance information",
    "": "Remittance slip number",
    "": "Receive bank name",
    "": "Receiver name",
    "": "Remittance amount",
    "": "Remittance date",
    "": "Information recorded in"
  }
}
</i18n>

<template>
  <el-card class="container">
    <div slot="header">
      <span v-t="''" />
      <el-button
        :disabled="loading"
        style="float: right; padding: 3px 0"
        type="text"
        @click="$router.push(`/project/${payment.project_id}`)">{{ $t('') }}</el-button>
    </div>
    <el-table
      v-loading="loading"
      :data="tableData"
      :show-header="false"
      border
      stripe>
      <el-table-column
        prop="label"
        label="" />
      <el-table-column
        prop="value"
        label=" " />
    </el-table>
  </el-card>
</template>

<script>
import { getPaymentById } from '@/api/payment'
export default {
  data () {
    return {
      loading: true,
      payment: {}
    }
  },
  computed: {
    tableData () {
      const { number, bank, name, amount, remitted_at, created_at } = this.payment

      return [{
        label: this.$t(''),
        value: number
      }, {
        label: this.$t(''),
        value: bank
      }, {
        label: this.$t(''),
        value: name
      }, {
        label: this.$t(''),
        value: amount
      }, {
        label: this.$t(''),
        value: remitted_at
      }, {
        label: this.$t(''),
        value: created_at
      }]
    }
  },
  created () {
    this.getPayment()
  },
  methods: {
    getPayment () {
      getPaymentById(this.$route.params.id).then(({ data }) => {
        this.payment = data
        this.loading = false
      }).catch(() => {
        this.loading = false
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  width: 600px;
  margin: 48px auto;
}
</style>



