<i18n>
{
  "en": {
    "": "Upload delivery file",
    "5000": "Remark (up to 5000 characters)",
    "": "File uploading, please wait",
    "": "Please upload the design file to deliver",
    "": "Successfully uploaded",
    "": "View the delivered design file"
  }
}
</i18n>

<template>
  <el-dialog
    :visible.sync="visible"
    :title="$t('')">
    <el-input
      v-model="form.remark"
      :rows="5"
      :placeholder="$t('5000')"
      :maxlength="5000"
      type="textarea"/>
    <my-upload
      ref="upload"
      :max-size="100"
      type="delivery_file"
      class="mt-12"
      @start="onUploadStart"
      @success="onUploadSuccess"
      @error="onUploadError"
      @remove="onUploadRemove"/>
    <my-alert
      v-if="hasSubmitted"
      class="mt-12">
      <a
        :href="project.delivery.file_url"
        target="_blank">{{ $t('') }}</a>
    </my-alert>
    <span
      slot="footer"
      class="dialog-footer">
      <el-button @click="onCancel">{{ $t('g.cancelBtn') }}</el-button>
      <el-button
        :loading="submitting"
        type="primary"
        @click="onConfirm">{{ $t('g.confirmBtn') }}</el-button>
    </span>
  </el-dialog>
</template>

<script>
import { deliverProjectById, updateDeliveryById } from '@/api/project'
export default {
  props: {
    project: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      visible: false,
      form: {
        remark: '',
        file_url: null
      },
      uploading: false,
      submitting: false
    }
  },
  computed: {
    // 
    hasSubmitted () {
      return !!this.project.delivery
    }
  },
  watch: {
    visible () {
      const { delivery } = this.project
      if (delivery) {
        this.form = {
          remark: delivery.remark,
          file_url: delivery.file_url
        }
      }
    }
  },
  methods: {
    show () {
      this.visible = true
    },
    onConfirm () {
      if (this.uploading) {
        this.$message.warning(this.$t(''))
        return false
      }
      if (!this.form.file_url) {
        return this.$message.warning(this.$t(''))
      }

      // dialog
      if (this.hasSubmitted) {
        const { form, project: { delivery } } = this
        if (form.remark === delivery.remark &&
        form.file_url === delivery.file_url) {
          this.visible = false
          return
        }
      }

      this.submitting = true

      // 
      const fn = this.hasSubmitted
        ? updateDeliveryById(this.project.delivery.id, this.form)
        : deliverProjectById(this.project.id, this.form)

      fn.then(({ data }) => {
        this.submitting = false
        this.visible = false
        this.$message.success(this.$t(''))
        this.$emit('delivered', data)
      }).catch(() => {
        this.submitting = false
      })
    },
    onCancel () {
      this.$refs.upload.cancel()
      this.visible = false
    },
    onUploadStart () {
      this.uploading = true
    },
    onUploadSuccess (data) {
      this.uploading = false
      this.form.file_url = data.path
    },
    onUploadError () {
      this.uploading = false
    },
    onUploadRemove () {
      this.form.file_url = null
    }
  }
}
</script>

<style>
</style>



