<i18n>
{
  "en": {
    "": "Upload Authentication Information",
    "": "Qualification certificates",
    " 20 ": "You can upload up to 20 qualification certificates, such as design certificates, degree certificates, etc.",
    "": "ID Number",
    "": "ID Card",
    "": "Upload photo",
    "": "Successful Operation",
    "50": "50 characters at most",
    "": "File uploading, please wait",
    "": "Has already committed, click to download",
    "": "Notice",
    "": "Once the id card information is uploaded, it cannot be modified!",
    "": "Please upload a portrait of the ID card!",
    "": "Once the id card information is uploaded, it cannot be modified. Is it confirmed?"
  }
}
</i18n>

<template>
  <div class="container">
    <h2
      v-t="''"
      class="mt0" />
    <el-form
      ref="form"
      label-position="top">
      <el-form-item :label="$t('')">
        <my-multi-upload
          ref="upload"
          :limit="20"
          :image-urls="user.qualification_urls"
          type="work_photo" />
        <el-card class="tips-card">
          <span v-t="' 20 '" />
        </el-card>
      </el-form-item>
      <el-form-item :label="$t('')" >
        <el-input
          :disabled="!!user.id_number"
          v-model="form.id_number"
          maxlength="100"/>
        <el-card class="tips-card">
          <h4
            v-t="''"
            class="m0 mb1 f-16" />
          <div v-t="''" />
          <div v-t="''" />
        </el-card>
      </el-form-item>
      <el-form-item :label="$t('')" >
        <my-alert v-if="user.id_card_url"><a
          :href="user.id_card_url"
          target="_blank">{{ $t('') }}</a></my-alert>
        <my-upload
          v-else
          :button-text="$t('')"
          :max-size="2"
          type="id_card"
          accept="image/jpeg, image/jpg, image/png, image/bmp"
          @start="onUploadStart"
          @success="onUploadSuccess"
          @error="onUploadError"
          @remove="onUploadRemove"/>
      </el-form-item>
      <el-form-item>
        <el-button
          :loading="loading"
          type="primary"
          @click="onSubmit">{{ $t('g.submitBtn') }}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { updateCurrentUserInfo } from '@/api/user'
export default {
  data () {
    return {
      form: {
        id_number: '',
        id_card_id: null
      },
      uploadingIdCard: false, // 
      loading: false // 
    }
  },
  computed: {
    user () {
      return this.$store.getters.userInfo
    }
  },
  created () {
    const user = this.$store.getters.userInfo
    this.form.id_number = user.id_number
  },
  beforeDestroy () {
    this.$refs.upload.clear() // 
  },
  methods: {
    onSubmit () {
      let qualificationUrls = this.$refs.upload.getImageUrls()
      if (qualificationUrls === false || this.uploadingIdCard) {
        return this.$message.warning(this.$t(''))
      }

      this.$confirm(this.$t(''), this.$t('g.notice'), {
        confirmButtonText: this.$t('g.confirmBtn'),
        cancelButtonText: this.$t('g.cancelBtn'),
        type: 'warning'
      }).then(() => {
        this.loading = true

        // 
        let dirtyForm = {}
        const { form, user } = this
        if (form.id_number !== user.id_number) dirtyForm.id_number = form.id_number
        if (form.id_card_id) dirtyForm.id_card_id = form.id_card_id
        dirtyForm.qualification_urls = qualificationUrls

        updateCurrentUserInfo(dirtyForm).then(({ data }) => {
          this.loading = false
          this.$store.commit('SET_USERINFO', data)
          this.$message.success(this.$t(''))
        }).catch(() => {
          this.loading = false
        })
      }).catch(() => {})
    },
    onUploadStart () {
      this.uploadingIdCard = true
    },
    onUploadSuccess (event) {
      this.uploadingIdCard = false
      this.form['id_card_id'] = event.id
    },
    onUploadError () {
      this.uploadingIdCard = false
    },
    onUploadRemove () {
      this.form['id_card_id'] = null
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  position: relative;
  padding: 24px 32px;
  .el-form {
    width: 600px;
  }
  .alert > a {
    line-height: 20px;
  }
}
.tips-card {
  position: absolute;
  top: 0px;
  left: 660px;
  width: 250px;
  font-size: 14px;
  line-height: 1.8em;
  color: rgba($color: #000000, $alpha: 0.65);
}
</style>



