<i18n>
{
  "en": {
    "": "Change Password",
    "": "Original password",
    "": "New password",
    "": "Confirm new password",
    " 6  25 ": "Password length is 6 to 25 characters",
    " ": "Reset",
    " ": "Submit",
    "": "Please enter the original password",
    "": "Please enter the new password",
    "": "Please confirm the new password",
    "": "Inconsistent new password",
    "": "Successfully change password"
  }
}
</i18n>

<template>
  <div class="container">
    <h2
      v-t="''"
      class="mt0" />
    <el-form
      ref="form"
      :rules="rules"
      :model="form"
      label-position="top">
      <el-form-item
        :label="$t('')"
        prop="password">
        <el-input
          v-model="form.password"
          auto-complete="off"
          type="password"/>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        prop="newPassword">
        <el-input
          v-model="form.newPassword"
          :placeholder="$t(' 6  25 ')"
          auto-complete="off"
          type="password"/>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        prop="confirmPassword">
        <el-input
          v-model="form.confirmPassword"
          auto-complete="off"
          type="password"/>
      </el-form-item>
      <el-form-item>
        <el-button @click="onReset()">{{ $t(' ') }}</el-button>
        <el-button
          :loading="loading"
          type="primary"
          @click="onSubmit">{{ $t(' ') }}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { changePassword } from '@/api/user'
export default {
  data () {
    return {
      form: {
        password: '',
        newPassword: '',
        confirmPassword: ''
      },
      rules: {
        password: {
          required: true, message: this.$t(''), trigger: 'blur'
        },
        newPassword: [
          { required: true, trigger: 'blur', message: this.$t('') },
          { min: 6, max: 25, trigger: 'blur', message: ' 6  25 ' },
          {
            validator: (rule, value, callback) => {
              if (this.form.confirmPassword) {
                this.$refs.form.validateField('confirmPassword')
              }
              callback()
            }
          }],
        confirmPassword: [
          { required: true, message: this.$t(''), trigger: 'blur' },
          {
            validator: (rule, value, callback) => {
              if (value !== this.form.newPassword) {
                callback(new Error(this.$t('')))
              } else {
                callback()
              }
            }
          }]
      },
      loading: false
    }
  },
  methods: {
    onReset () {
      this.$refs.form.resetFields()
    },
    onSubmit () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.loading = true
          const { password, newPassword } = this.form
          changePassword(password, newPassword).then(() => {
            this.loading = false
            this.$message.success(this.$t(''))
            this.$refs.form.resetFields()
          }).catch(() => {
            this.loading = false
          })
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  position: relative;
  padding: 24px 32px;
  .el-form {
    width: 600px;
  }
}
</style>



