<i18n>
{
  "zh": {
    "declined": ": {refusalCause}",
    "paymentHelp": " \n: \n: , 10000; , 10000. \n: , 5000."
  },
  "en": {
    "declined": "You have declined the project. Refusal cause: {refusalCause}",
    "": "The project has been canceled",
    "": "The project is reviewing",
    "": "The project has not passed review",
    "": "The project mode is Free. Every designers can apply and participate.",
    "": "The project mode is Invite, and is visible only to invited designers.",
    "": "The project mode is Specify, and is visible only to specified designers.",
    "": "The project has remitted the design fee",
    "": "Favorite",
    "": "Unfavorite",
    "": "Apply",
    "": "Apply the project",
    "": "Accept invitation",
    "": "Decline invitation",
    "": "Has accepted invitation",
    "": "Has declined invitation",
    "": "Are you sure to accept the invitation from the party and to participate in the project?",
    "": "Are you sure you reject the invitation from the party? This operation cannot undo",
    "300": "Please enter the refusal reason, up to 300 characters",
    "": "You have not accepted the invitation within the specified time and are unable to participate in the project.",
    "": "Please upload the design file within the specified delivery time.",
    "": "This project has been complete. You have not uploaded the design file within the specified delivery time.",
    "": "Successfully apply",
    "": "Cancel apply",
    "": "This operation will cancel the application for the project. Is it confirmed?",
    "": "Has uploaded delivery file",
    "": "Uploaded delivery file",

    "": "Edit the project",
    "": "Cancel publish",
    "": "Complete the project",

    "": "This operation will unpublish the project and is not recoverable. Is it confirmed?",
    "": "Successfully canceled",
    "": "Party",
    "": "Published at",
    "": "Designers applying",
    "": "Designers working",
    "": "Project is complete",
    "": "Project types",
    "": "Project features",
    "": "Project description",
    "": "Project keywords",
    "": "Design depth",
    "": "Delivery time",
    "": "How long do you want to find a designer?",
    "": "How much would you want to pay for the designer?",
    "": "Download file",
    "": "Upload file",
    "": "Allow upload only one file",
    "": "File uploading, please wait",
    "200": "Say something so that the party can know you quickly (200 characters at most)",
    "": "My application",
    "": "View project info",
    "": "Collapse",
    "": "Sign in to view complete project info",
    "": "My delivery file",
    "": "Delivered at",

    "": "Successful operation",

    "": "Updated at",
    "": "Edit",
    "": "Design fee destribution scheme",
    "": "Describe how to pay design fee",
    "": "Please describe how to pay design fee",
    "paymentHelp": "Please describe your design fee payment plan accurately. Please explain to whom and how much the design fee is paid. Design fees can only be paid to designers who successfully upload design files. \nFor example: \nThe First Prize: Wang, 10000 RMB; Zhang, 10000 RMB. \nThe Second Prize: Cook, 5000 RMB; Jobs, 5000 RMB."
  }
}
</i18n>

<template>
  <div>
    <div class="project-header card">
      <my-alert
        v-if="isCanceled"
        :title="$t('')"
        type="warning"
        class="mb2"/>
      <my-alert
        v-if="isReviewing"
        :title="$t('')"
        type="warning"
        class="mb2"/>
      <my-alert
        v-if="isReviewFailed"
        :title="$t('') + (project.review_message ? '' + project.review_message : '')"
        type="warning"
        class="mb2"/>
      <template v-if="$isDesigner()">
        <my-alert
          v-if="invitable && isNotViewed && !isTendering"
          :title="$t('')"
          type="warning"
          class="mb2"/>
        <my-alert
          v-if="canDeliver && isWorking && !isDeliverd"
          :title="$t('')"
          type="warning"
          class="mb2"/>
        <my-alert
          v-if="canDeliver && isCompleted && !isDeliverd"
          :title="$t('')"
          type="warning"
          class="mb2"/>
        <my-alert
          v-if="isDeclined"
          :title="$t('declined', { refusalCause: project.invitation.refusal_cause })"
          type="warning"
          class="mb2"/>
      </template>
      <my-alert
        v-if="project.mode === 'free'"
        :title="$t('')"
        class="mb2"/>
      <my-alert
        v-if="project.mode === 'invite'"
        :title="$t('')"
        class="mb2"/>
      <my-alert
        v-if="project.mode === 'specify'"
        :title="$t('')"
        class="mb2"/>
      <my-alert
        v-if="project.has_remitted"
        :title="$t('')"
        type="success"
        class="mb2"/>
      <div class="project-header__title-area">
        <h1 class="project-header__title">{{ project.title }}</h1>
        <template v-if="$isDesigner()">
          <el-button
            v-if="project.favoriting"
            icon="el-icon-star-off"
            size="mini"
            @click="onUnfavorite"
          >{{ $t('') }} ({{ project.favorite_count }})</el-button>
          <el-button
            v-else
            icon="el-icon-star-off"
            size="mini"
            @click="onFavorite"
          >{{ $t('') }} ({{ project.favorite_count }})</el-button>
          <template v-if="appliable">
            <el-button
              v-if="canCancelApply"
              size="mini"
              @click="onCancelApply"
            >{{ $t('') }}</el-button>
            <el-button
              v-if="canApply"
              type="primary"
              size="mini"
              @click="dialogVisible = true"
            >{{ $t('') }}</el-button>
          </template>
          <template v-if="invitable">
            <el-button
              v-if="isAccepted || isDeclined"
              size="mini"
              disabled
            >{{ $t(isAccepted ? '' : '') }}</el-button>
            <template v-else-if="isTendering">
              <el-button
                type="success"
                size="mini"
                plain
                @click="onAcceptInvitation"
              >{{ $t('') }}</el-button>
              <el-button
                type="danger"
                size="mini"
                plain
                @click="onDeclineInvitation"
              >{{ $t('') }}</el-button>
            </template>
          </template>
          <el-button
            v-if="isDeliverd"
            size="mini"
            type="success"
            disabled>{{ $t('') }}</el-button>
          <el-button
            v-else-if="canDeliver && isWorking"
            size="mini"
            type="primary"
            @click="onShowDeliverDialog">{{ $t('') }}</el-button>
        </template>
        <template v-if="$isParty() && isPublisher">
          <el-button
            v-if="editable"
            size="mini"
            @click="onEdit"
          >{{ $t('') }}</el-button>
          <el-button
            v-if="cancelable"
            size="mini"
            @click="onCancelPublish"
          >{{ $t('') }}</el-button>
          <el-button
            v-if="isWorking"
            plain
            size="mini"
            @click="onShowPaymentDialog"
          >{{ $t('') }}</el-button>
        </template>
      </div>
      <div class="project-header__info">
        <div class="project-header__info-item">
          <span class="black-85">{{ $t('') }}: </span>
          <my-avatar :avatar-url="project.user.avatar_url"/>
          <router-link :to="'/profile?uid='+project.user.id">{{ project.user.name }}</router-link>
        </div>
        <div class="project-header__info-item">
          <span class="black-85">{{ $t('') }}: </span>
          {{ project.created_at }}
        </div>
      </div>
      <el-steps
        :active="stepIndex"
        process-status="process"
        finish-status="success"
        class="mt3">
        <el-step :title="$t('')" />
        <el-step :title="$t('')" />
        <el-step :title="$t('')" />
      </el-steps>
    </div>
    <div class="main-container">
      <div
        v-if="isPublisher && isCompleted"
        class="card">
        <h3
          v-t="''"
          class="mt0" />
        <el-button
          type="text"
          style="position:absolute;right:24px;top:16px;"
          @click="onShowPaymentDialog">{{ $t('') }}</el-button>
        <p v-text="project.payment_remark" />
        <p class="m0 mt1 f-12 black-65">{{ $t('') }}{{ project.payment_remark_updated_at }}</p>
      </div>
      <div
        v-if="$isDesigner() && isDeliverd"
        class="card">
        <h3
          v-t="''"
          class="mt0" />
        <el-button
          v-if="canUpdateDelivery"
          type="text"
          style="position:absolute;right:24px;top:16px;"
          @click="onShowDeliverDialog">{{ $t('') }}</el-button>
        <p
          v-if="project.delivery.remark"
          v-text="project.delivery.remark" />
        <my-alert class="mt-12">
          <a
            :href="project.delivery.file_url"
            target="_blank">{{ $t('') }}</a>
        </my-alert>
        <p class="m0 mt1 f-12 black-65">{{ $t('') }}{{ project.delivery.created_at }}</p>
      </div>
      <div
        v-if="$isDesigner() && project.applying"
        class="card">
        <h3
          v-t="''"
          class="mt0" />
        <p v-text="project.application.remark" />
        <my-alert
          v-if="project.application.application_file_url"
          class="mt-12">
          <a
            :href="project.application.application_file_url"
            target="_blank">{{ $t('') }}</a>
        </my-alert>
        <p class="m0 mt1 f-12 black-65">{{ $t('') }}{{ project.application.created_at }}</p>
      </div>
      <div
        v-loading="loading"
        class="card">
        <template v-if="showInfo">
          <el-button
            type="text"
            style="position:absolute;right:24px;top:16px;"
            @click="showInfo = false">{{ $t('') }}</el-button>
          <div class="project-info">
            <h3 v-t="''" />
            <p>{{ project.types.join('/') }}</p>
            <h3 v-t="''" />
            <p>{{ project.features.join('/') }}</p>
            <h3 v-t="''" />
            <el-tag
              v-for="keyword in project.keywords"
              :key="keyword"
              type="info"
              class="mr1">{{ keyword }}</el-tag>
            <template v-if="isSignIn">
              <h3 v-t="''" />
              <p
                class="pre-wrap"
                v-text="project.description" />
              <my-alert
                v-if="project.project_file_url"
                class="mt-12">
                <a
                  :href="project.project_file_url"
                  target="_blank">{{ $t('') }}</a>
              </my-alert>
              <h3 v-t="''" />
              <p v-text="project.depth" />
              <h3 v-t="''" />
              <p v-text="project.delivery_time" />
              <h3 v-t="''" />
              <p v-text="project.find_time" />
              <h3 v-t="''" />
              <p v-text="project.payment" />
            </template>
          </div>
        </template>
        <el-button
          v-else
          type="text"
          @click="showInfo = true">{{ $t('') }}</el-button>
      </div>
      <router-link
        v-if="!isSignIn"
        :to="`/signin?returnUrl=${$route.fullPath}`">
        <el-button
          class="block w-100 shadow"
          type="primary">{{ $t('') }}</el-button>
      </router-link>
      <template v-if="isPublisher">
        <delivery-list
          v-if="isWorking || isCompleted"
          :project="project" />
        <application-list
          v-if="appliable"
          :project="project" />
        <invitation-list
          v-if="invitable"
          :project="project" />
      </template>
    </div>
    <el-dialog
      :visible.sync="dialogVisible"
      :title="$t('')">
      <el-input
        v-model="applicationForm.remark"
        :rows="5"
        :placeholder="$t('200')"
        type="textarea"/>
      <my-upload
        ref="upload"
        :max-size="50"
        type="application_file"
        class="mt-12"
        @start="onUploadStart"
        @success="onUploadSuccess"
        @error="onUploadError"
        @remove="onUploadRemove"/>
      <span
        slot="footer"
        class="dialog-footer">
        <el-button @click="onDialogCancel">{{ $t('g.cancelBtn') }}</el-button>
        <el-button
          :loading="dialogButtonLoading"
          type="primary"
          @click="onDialogConfirm">{{ $t('g.confirmBtn') }}</el-button>
      </span>
    </el-dialog>
    <deliver-dialog
      ref="deliverDialog"
      :project="project"
      @delivered="onDeliverd" />
    <el-dialog
      :visible.sync="paymentDialog.visible"
      :title="$t('')">
      <p
        v-t="'paymentHelp'"
        class="mt0 black-45 f-14"
        style="white-space:pre-wrap" />
      <el-input
        v-model="paymentDialog.content"
        :rows="5"
        :placeholder="$t('')"
        :maxlength="1000"
        type="textarea"/>
      <span slot="footer">
        <el-button @click="paymentDialog.visible = false">{{ $t('g.cancelBtn') }}</el-button>
        <el-button
          :loading="paymentDialog.submitting"
          :disabled="!paymentDialog.content"
          type="primary"
          @click="onSubmitPaymentRemark">{{ $t('g.confirmBtn') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
/* eslint eqeqeq: "off" */
import { Project, ProjectInvitation } from '@/services/constants'
import {
  getProjectById,
  cancelProjectById,
  updateProjectPaymentById,
  favoriteProjectById,
  unfavoriteProjectById,
  applyProjectById,
  cancelApplyProjectById,
  acceptInvitationByProjectId,
  declineInvitationByProjectId } from '@/api/project'
import DeliverDialog from './components/DeliverDialog'
import ApplicationList from './components/ApplicationList'
import InvitationList from './components/InvitationList'
import DeliveryList from './components/DeliveryList'
export default {
  components: { DeliverDialog, ApplicationList, InvitationList, DeliveryList },
  data () {
    return {
      project: {
        id: null,
        title: '',
        types: [],
        features: [],
        keywords: [],
        favorite_count: 0,
        user: {},
        application: {},
        applications: [],
        invitations: [],
        invitation: { // 
          status: null,
          refusal_cause: ''
        }
      },
      loading: false, // 

      /**
       *  Dialog
       */
      dialogVisible: false,
      dialogUploading: false, // 
      dialogButtonLoading: false, // 
      applicationForm: { // 
        remark: '',
        application_file_id: null
      },

      /**
       *  Dialog
       */
      paymentDialog: {
        visible: false,
        content: '',
        submitting: false
      },

      showInfo: !this.$isParty() // 
    }
  },
  computed: {
    // 
    isSignIn () {
      return !!this.$store.getters.uid
    },
    // id
    id () {
      return this.$route.params.id
    },
    // 
    stepIndex () {
      switch (this.project.status) {
        case Project.STATUS_TENDERING:
          return 0
        case Project.STATUS_WORKING:
          return 1
        case Project.STATUS_COMPLETED:
          return 2
      }
    },
    // 
    isCanceled () {
      return this.project.status == Project.STATUS_CANCELED
    },
    // 
    isReviewing () {
      return this.project.status == Project.STATUS_REVIEWING
    },
    // 
    isReviewFailed () {
      return this.project.status == Project.STATUS_REVIEW_FAILED
    },
    // 
    isTendering () {
      return this.project.status == Project.STATUS_TENDERING
    },
    // 
    isWorking () {
      return this.project.status == Project.STATUS_WORKING
    },
    // 
    isCompleted () {
      return this.project.status == Project.STATUS_COMPLETED
    },
    // 
    appliable () {
      return this.project.mode === 'free'
    },
    // 
    invitable () {
      return this.project.mode === 'invite' || this.project.mode === 'specify'
    },
    // 
    isPublisher () {
      return this.$uid() && this.project.user.id == this.$uid()
    },
    // 
    editable () {
      const { project } = this
      const { user } = this.project
      return user.id == this.$uid() && // 
        (project.status == Project.STATUS_REVIEW_FAILED || // 
         project.status == Project.STATUS_REVIEWING) // 
    },
    // 
    cancelable () {
      const { project } = this
      const { user } = this.project
      return user.id == this.$uid() && // 
        (project.status == Project.STATUS_TENDERING || // 
         project.status == Project.STATUS_REVIEW_FAILED || // 
         project.status == Project.STATUS_REVIEWING) // 
    },
    // 
    canApply () {
      const { project, appliable } = this
      return appliable && // 
          project.status == Project.STATUS_TENDERING && // 
          !project.applying // 
    },
    // 
    canCancelApply () {
      const { project } = this
      return project.status == Project.STATUS_TENDERING && project.applying
    },
    // 
    isAccepted () {
      const { project, invitable } = this
      return invitable && project.invitation.status === ProjectInvitation.STATUS_ACCEPTED
    },
    // 
    isDeclined () {
      const { project, invitable } = this
      return invitable && project.invitation.status === ProjectInvitation.STATUS_DECLINED
    },
    // 
    isNotViewed () {
      const { project, invitable } = this
      return invitable && project.invitation.status === ProjectInvitation.STATUS_NOT_VIEWED
    },
    // 
    isDeliverd () {
      const { project } = this
      return !!project.delivery
    },
    // 
    canDeliver () {
      const { isAccepted, project: { applying } } = this
      return isAccepted || applying
    },
    // 
    canUpdateDelivery () {
      const { project } = this
      return project.status == Project.STATUS_WORKING // 
    }
  },
  created () {
    this.getProject().then(() => {
      this.showInfo = !this.isPublisher
      this.paymentDialog.content = this.project.payment_remark
    })
  },
  methods: {
    getProject () {
      this.loading = true
      return getProjectById(this.id).then(({ data }) => {
        this.loading = false
        this.project = data
      }).catch(() => {
        this.loading = false
      })
    },
    /**
     * 
     */
    onCancelApply () {
      this.$confirm(this.$t(''), this.$t('g.notice'), {
        confirmButtonText: this.$t('g.confirmBtn'),
        cancelButtonText: this.$t('g.cancelBtn'),
        type: 'warning'
      }).then(() => {
        cancelApplyProjectById(this.project.id).then(() => {
          this.project.applying = false
          this.$message({
            type: 'success',
            message: this.$t('')
          })
        })
      }).catch(() => {})
    },
    onFavorite () {
      favoriteProjectById(this.project.id).then(() => {
        this.project.favoriting = true
        this.project.favorite_count++
      })
    },
    onUnfavorite () {
      unfavoriteProjectById(this.project.id).then(() => {
        this.project.favoriting = false
        this.project.favorite_count--
      })
    },
    onAcceptInvitation () {
      this.$confirm(this.$t(''), this.$t('g.notice'), {
        confirmButtonText: this.$t('g.confirmBtn'),
        cancelButtonText: this.$t('g.cancelBtn'),
        type: 'warning'
      }).then(() => {
        acceptInvitationByProjectId(this.project.id).then(({ data }) => {
          this.project.invitation = data
          this.$message({
            type: 'success',
            message: this.$t('')
          })
        })
      }).catch(() => {})
    },
    onDeclineInvitation () {
      this.$prompt(this.$t(''), this.$t('g.notice'), {
        confirmButtonText: this.$t('g.confirmBtn'),
        cancelButtonText: this.$t('g.cancelBtn'),
        inputPattern: /^(?=.*?\S)[\s\S]{0,300}$/,
        inputPlaceholder: this.$t('300'),
        inputErrorMessage: this.$t('300')
      }).then(({ value }) => {
        declineInvitationByProjectId(this.project.id, value).then(({ data }) => {
          this.project.invitation = data
          this.$message({
            type: 'success',
            message: this.$t('')
          })
        })
      }).catch(() => {})
    },
    onShowDeliverDialog () {
      this.$refs.deliverDialog.show()
    },
    onDeliverd (data) {
      this.project.delivery = data
    },
    /**
     * 
     */
    onCancelPublish () {
      this.$confirm(this.$t(''), this.$t('g.notice'), {
        confirmButtonText: this.$t('g.confirmBtn'),
        cancelButtonText: this.$t('g.cancelBtn'),
        type: 'warning'
      }).then(() => {
        cancelProjectById(this.project.id).then(() => {
          this.project.status = Project.STATUS_CANCELED
          this.project.canceled_at = this.$dayjs().format('YYYY-MM-DD HH:mm:ss')
          this.$message({
            type: 'success',
            message: this.$t('')
          })
        })
      }).catch(() => {})
    },
    onEdit () {
      this.$router.push(`/project/${this.id}/edit`)
    },
    onShowPaymentDialog () {
      this.paymentDialog.visible = true
    },
    onSubmitPaymentRemark () {
      this.paymentDialog.submitting = true
      const markAsComplete = this.isWorking
      updateProjectPaymentById(this.id, this.paymentDialog.content, markAsComplete)
        .then(({ data }) => {
          this.project = data
          this.paymentDialog.submitting = false
          this.paymentDialog.visible = false
          this.$message.success(this.$t(''))
        })
        .catch(() => {
          this.paymentDialog.submitting = true
        })
    },
    /**
     * Dialog
     */
    onDialogCancel () {
      this.$refs.upload.cancel()
      this.dialogVisible = false
    },
    onDialogConfirm () {
      if (this.dialogUploading) {
        this.$message.warning(this.$t(''))
        return false
      }

      this.dialogButtonLoading = true
      applyProjectById(this.project.id, this.applicationForm).then(({ data }) => {
        this.dialogButtonLoading = false
        this.dialogVisible = false
        this.$message.success(this.$t(''))
        this.project.applying = true
        this.project.application = data
      }).catch(() => {
        this.dialogButtonLoading = false
      })
    },
    onUploadStart () {
      this.dialogUploading = true
    },
    onUploadSuccess (data) {
      this.dialogUploading = false
      this.applicationForm.application_file_id = data.id
    },
    onUploadError () {
      this.dialogUploading = false
    },
    onUploadRemove () {
      this.applicationForm.application_file_id = null
    }
  }
}
</script>

<style lang="scss" scoped>
.project-header {
  width: 100%;
  padding: 24px calc((100% - 1000px) / 2);
  &__title-area {
    display: flex;
    align-items: flex-start;
    margin: 0 0 16px;
    vertical-align: middle;
  }
  &__title {
    flex: 1;
    margin: 0;
    font-size: 20px;
    color: rgba(0, 0, 0, 0.85);
  }
  &__info {
    width: 600px;
    &-item {
      display: inline-block;
      width: 290px;
      font-size: 14px;
      color: rgba(0, 0, 0, 0.65);
      .avatar {
        width: 20px;
        height: 20px;
        vertical-align: bottom;
      }
    }
  }
}
.main-container {
  .card {
    margin-bottom: 24px;
    padding: 24px;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.85);
    &:last-child {
      margin-bottom: 0;
    }
  }
  .project-info {
    h3 {
      margin-top: 32px;
      &:first-child {
        margin-top: 0;
      }
    }
    p {
      margin: 8px 0;
      line-height: 1.81em;
    }
  }
}
.alert {
  font-size: 14px;
}
</style>



