<i18n>
{
  "zh": {
    "countdown": "({count})"
  },
  "en": {
    "": "Sign up with email",
    "": "Email",
    "": "User type",
    "": "Designer",
    "": "Party",
    "": "Real name",
    "": "Send verification code",
    "": "Verification code",
    "": "Password",
    " ": "Submit",
    "": "Please select user type",
    "2~50": "Please enter your real name (between 2 to 50 characters)",
    "": "Please enter a valid email address",
    "6": "Please enter 6 characters verification code",
    "": "Please enter a password",
    " 6  25 ": "Password length is 6 to 25 characters",
    "": "Successfully sign up",
    "": "Sign in",
    "countdown": "Resend verification code ({count})"
  }
}
</i18n>

<template>
  <section class="container">
    <h1 v-t="''" />
    <el-form
      ref="form"
      :rules="rules"
      :model="form"
      size="small">
      <el-form-item
        :label="$t('')"
        prop="type">
        <el-radio-group v-model="form.type">
          <el-radio label="party">{{ $t('') }}</el-radio>
          <el-radio label="designer">{{ $t('') }}</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        prop="name">
        <el-input
          v-model="form.name"
          type="text"/>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        prop="email">
        <el-input v-model="form.email">
          <el-button
            slot="append"
            :loading="sending"
            :disabled="!!count"
            @click="onSend">{{ sendButtonText }} </el-button>
        </el-input>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        prop="verification_code">
        <el-input v-model="form.verification_code"/>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        prop="password">
        <el-input
          v-model="form.password"
          type="text"/>
      </el-form-item>
      <el-form-item>
        <el-button
          :loading="loading"
          type="primary"
          @click="onSubmit">{{ $t(' ') }}</el-button>
        <router-link
          to="/signin"
          class="center"
          style="display:block;">{{ $t('') }}</router-link>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
import { namePattern, validateEmail } from '@/utils/validate'
import { sendEmailCode } from '@/api/user'
export default {
  data () {
    return {
      form: {
        type: null,
        name: '',
        email: '',
        password: '',
        verification_code: ''
      },
      rules: {
        type: { required: true, message: this.$t('') },
        name: { required: true, pattern: namePattern, min: 1, max: 50, message: this.$t('2~50') },
        email: { required: true, type: 'email', message: this.$t(''), trigger: 'blur' },
        verification_code: { required: true, len: 6, message: this.$t('6'), trigger: 'blur' },
        password: [
          { required: true, trigger: 'blur', message: this.$t('') },
          { min: 6, max: 25, trigger: 'blur', message: ' 6  25 ' }
        ]
      },
      loading: false,
      sending: false, // 
      count: 0, // 
      timer: null // 
    }
  },
  computed: {
    sendButtonText () {
      const { count } = this
      if (count) {
        return this.$t('countdown', { count })
      } else {
        return this.$t('')
      }
    }
  },
  methods: {
    onSend () {
      if (validateEmail(this.form.email)) {
        this.sending = true
        const { email } = this.form
        sendEmailCode(email, 'register').then(() => {
          this.sending = false
          this.count = 60
          this.countDown()
        }).catch(() => {
          this.sending = false
        })
      } else {
        this.$message.warning(this.$t(''))
      }
    },
    onSubmit () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.loading = true
          return this.$store.dispatch('signUp', this.form).then(() => {
            this.loading = false
            this.$message.success(this.$t(''))
            this.$router.push({ path: '/feed' })
          }).catch(() => {
            this.loading = false
          })
        }
      })
    },
    countDown () {
      if (this.count > 0) {
        this.count--
        this.timer = setTimeout(() => {
          this.countDown()
        }, 1000)
      } else {
        clearTimeout(this.timer)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  width: 400px;
  padding: 30px 30px 0;
  margin: 24px auto 0;
  background: #ffffff;
  border: 1px solid #d0d3d6;
  border-radius: 4px;
}
h1 {
  margin: 0 0 24px 0;
  color: #000;
  text-align: center;
  font-size: 24px;
  font-weight: 400;
}
.el-button {
  width: 100%;
}
</style>



