<i18n>
{
  "en": {
    "": "Change Email Address",
    "": "Current email address",
    "": "Have not bound email yet",
    "": "You can sign in the website or receive notifications via email after binding and activation",
    "": "New and original email address cannot be same",
    "": "Your email has not been activated. Please go to the email to view your activation email. If you have not received the message or the message has expired, you can",
    "": "Resend activation email",
    "": "Sended",
    "": "Activated",
    "": "New email address",
    " ": "Submit",
    "": "Please enter a valid email address",
    "": "The system has sent an activation email to the address. Please check it.",
    "": "The binding email is successfully modified. The system has sent an activation email to the address. Please check it."
  }
}
</i18n>

<template>
  <div class="container">
    <h2
      v-t="''"
      class="mt0" />
    <el-form
      ref="form"
      label-position="top">
      <el-form-item :label="$t('')">
        <el-input
          :value="$store.getters.userInfo.email || $t('')"
          disabled/>
        <template v-if="$store.getters.userInfo.email">
          <my-alert
            v-if="isEmailNotActivated"
            class="alert">
            <div>
              {{ $t('') }}
              <el-button
                v-if="!sended"
                :loading="sending"
                type="text"
                @click="onSend">{{ $t('') }} </el-button>
              <el-button
                v-else
                disabled
                type="text">{{ $t('') }} </el-button>
            </div>
          </my-alert>
          <my-alert
            v-else
            :title="$t('')"
            class="alert"/>
        </template>
        <el-card class="tips-card">{{ $t('') }}</el-card>
      </el-form-item>
      <el-form-item :label="$t('')">
        <el-input v-model="email" />
      </el-form-item>
      <el-form-item>
        <el-button
          :loading="loading"
          type="primary"
          @click="onSubmit">{{ $t(' ') }}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { validateEmail } from '@/utils/validate'
import { updateCurrentUserInfo, sendActiveMail } from '@/api/user'
export default {
  data () {
    return {
      email: '',
      loading: false,
      sending: false,
      sended: false
    }
  },
  computed: {
    isEmailNotActivated () {
      return this.$store.getters.userInfo.email && !this.$store.getters.userInfo.email_activated
    }
  },
  methods: {
    onSubmit () {
      const { email } = this
      if (!validateEmail(email)) {
        return this.$message.warning(this.$t(''))
      }
      if (email === this.$store.getters.userInfo.email) {
        return this.$message.warning(this.$t(''))
      }
      this.loading = true
      updateCurrentUserInfo({ email }).then(({ data }) => {
        this.loading = false
        this.email = ''
        this.$store.commit('SET_USERINFO', data)
        this.$message.success(this.$t(''))
      }).catch(() => {
        this.loading = false
      })
    },
    onSend () {
      this.sending = true
      sendActiveMail().then(() => {
        this.sending = false
        this.sended = true
        this.$message.success(this.$t(''))
      }).catch(() => {
        this.sending = false
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  position: relative;
  padding: 24px 32px;
  .el-form {
    width: 600px;
    .alert {
      margin-top: 12px;
      line-height: 20px;
      color: rgba(0, 0, 0, 0.85);
      .el-button {
        padding-top: 0;
        padding-bottom: 0;
      }
    }
  }
}
.tips-card {
  position: absolute;
  top: 0px;
  left: 660px;
  width: 250px;
  font-size: 14px;
  line-height: 1.8em;
  color: rgba($color: #000000, $alpha: 0.65);
}
</style>



