<i18n>
{
  "en": {
    "": "Remittance information",
    "": "The following is the remittance information entered by the administrator:",
    "": "The remittance information you filled out is:",
    "": "Please fill in the following remittance information accurately for the staff to enter: remittance slip number, remittance bank name, remitter name, remittance amount, remittance date",
    "": "Remittance slip number",
    "": "Remittance bank name",
    "": "Remitter name",
    "": "Remittance amount",
    "": "Remittance date",
    "": "Information recorded in",
    " ": "Edit"
  }
}
</i18n>

<template>
  <el-dialog
    :visible.sync="visible"
    :title="$t('')"
    @closed="onClosed">
    <template v-if="project.confirmed_remittance">
      <p
        v-t="''"
        class="mt0 black-45 f-14" />
      <el-table
        :data="tableData"
        :show-header="false"
        border
        stripe>
        <el-table-column
          prop="label"
          label="" />
        <el-table-column
          prop="value"
          label=" " />
      </el-table>
      <div class="f-14 black-45">
        <p v-t="''" />
        <p v-text="project.remittance" />
      </div>
    </template>
    <template v-else>
      <el-input
        v-model="content"
        :disabled="inputDisabled"
        :rows="5"
        :placeholder="$t('')"
        :maxlength="1000"
        type="textarea"/>
      <p
        v-t="''"
        v-if="content"
        class="black-45 mb0" />
      <span slot="footer">
        <el-button @click="visible = false">{{ $t('g.cancelBtn') }}</el-button>
        <el-button
          v-if="inputDisabled"
          @click="inputDisabled = false">{{ $t(' ') }}</el-button>
        <el-button
          v-else
          :loading="submitting"
          :disabled="!content"
          type="primary"
          @click="onConfirm">{{ $t('g.confirmBtn') }}</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { updateProjectRemittanceById } from '@/api/project'
export default {
  data () {
    return {
      project: {
        remittance: '',
        remittance_submitted_at: '',
        confirmed_remittance: null
      },
      visible: false,
      content: '',
      inputDisabled: false, // 
      submitting: false
    }
  },
  computed: {
    tableData () {
      const { confirmed_remittance } = this.project

      if (!confirmed_remittance) return []

      const { number, bank, name, amount, remitted_at, created_at } = confirmed_remittance

      return [{
        label: this.$t(''),
        value: number
      }, {
        label: this.$t(''),
        value: bank
      }, {
        label: this.$t(''),
        value: name
      }, {
        label: this.$t(''),
        value: amount
      }, {
        label: this.$t(''),
        value: remitted_at
      }, {
        label: this.$t(''),
        value: created_at
      }]
    }
  },
  methods: {
    show (project) {
      this.project = project
      this.content = project.remittance
      this.inputDisabled = !!project.remittance
      this.visible = true
    },
    onConfirm () {
      this.submitting = true
      updateProjectRemittanceById(this.project.id, this.content).then(({ data }) => {
        this.submitting = false
        this.visible = false
        this.$message.success(this.$t(''))

        // 
        this.project.remittance = data.remittance
        this.project.remittance_submitted_at = data.remittance_submitted_at
      }).catch(() => {
        this.submitting = false
      })
    },
    onClosed () {
      Object.assign(this.$data, this.$options.data())
    }
  }
}
</script>

<style>
</style>



